<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Editar el Financiamiento
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarFinanciamiento()">
          <v-text-field
            v-model="financing.type"
            label="Tipo de Financiamiento"
            outlined
            prepend-icon="mdi-account-tie"
            required
          />
          <v-text-field
            v-model="financing.description"
            label="Descripción"
            outlined
            prepend-icon="mdi-label"
            required
          />
          <v-text-field
            v-model="financing.entity"
            label="Entidad Financiera"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="financing.support"
            label="Sustento"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="financing.office"
            label="Oficina"
            prepend-icon="mdi-cash"
            outlined
            required
          />
          <v-text-field
            v-model="financing.account_executive"
            label="Sectorista"
            prepend-icon="mdi-account"
            outlined
            required
          />

          <v-card-actions>
            <v-btn
              to="/ventas/financiamiento"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlFinanciamiento = 'https://sistema-adv.herokuapp.com/api/financing/'

  export default {
    name: 'EditarFinanciamiento',
    data () {
      return {
        financing: {
          type: '',
          description: '',
          entity: '',
          support: '',
          office: '',
          account_executive: '',
        },
      }
    },
    mounted () {
      this.activarEdicion()
    },
    methods: {
      activarEdicion () {
        this.id = this.$route.params.id
        axios.get(urlFinanciamiento + this.id).then(res => {
          this.financing = res.data
        }).catch(e => {
          console.log(e)
        })
      },
      guardarFinanciamiento () {
        const router = this.$router
        const params = this.financing

        axios.put(urlFinanciamiento + this.id, params).then((res) => {
          // console.log(res);
          router.push('/ventas/financiamiento')
        })
      },
    },
  }
</script>
